.logo {
    margin-top: 20px;
    align-items: center;
    text-align: center;
    display: flex;
    padding: 10px;
    gap: 20px;
}

.logo-icon {
    font-size: 1.4rem;
}

.menu {
    width: 15vw;
    height: 94vh;
    gap: 20px;
    background-color: #222831;
    color: #eeeeee;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    box-shadow: 0 5px 10px rgb(20, 19, 19);


}

.menu-list {
    display: flex;
    flex-direction: column;
    padding: 10px;
    gap: 30px;
}

.menu-list .item {
    text-decoration: none;
    color: #eeeeee;
    font-size: 1.1rem;
    display: flex;
    gap: 30px;
    transition: 0.3s ease-in-out;   
    padding: 10px;
    border-radius: 10px;
}

.menu-list .item:hover,
.active {
    background-color:#393e46 !important;

}

