.card--container {
    display: flex;
    gap: 20px;
    align-items: center;
    margin-top: 2rem;
}

.card {
    flex: 1;
    background-color:#393e46;
    padding: 20px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    transition: 0.5s ease-in-out;
}

.card:hover {
    transform: scale(1.2);
    cursor: pointer;
    box-shadow: 0 5px 10px rgb(20, 19, 19);
}

.card--cover {
    background-color:#393e46;
    /* background-color: #222831; */
    font-size: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 20px;
    border-radius: 10px; */
    color: #eeeeee;
}

.card--title {
    width: 100%;
    text-align: center;
    padding: 10px 0;
    font-size: 14px;
    border-radius: 10px;
    /* background-color: #222831; */
}