.list--header {
      display: flex;
      margin:  2rem 0;
      justify-content: space-between;

}

.list--header h2 {
      color: #eeeeee;
}

.list--header select {
      border-radius: 10px;
      border: none;
      padding: 10px 14px;
      border: 1px solid rgb(83, 83, 83);
      color: #eeeeee;
      background-color: #222831;
}

.list--container {
      display: flex;
      flex-direction: column;
      gap: 20px;
}

.list {
      display: flex;
      padding: 10px;
      align-items: center;
      justify-content: space-between;
      transition: 0.3s ease-in-out;
      border-bottom: 0.5px solid #eeeeee;
}

.teacher--detail {
      display: flex;
      align-items: center;
      gap: 20px;
      color: #eeeeee;
}

.teacher--detail img {
      width: 50px;
      border-radius: 50%;
}

.list:hover {
      background-color:#393e46;
      border-radius: 10px;
      box-shadow: 0 5px 10px rgb(35, 34, 34)
}