.content--header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header--title {
    color: #eeeeee;
}

.header--activity {
     display: flex;
     align-items: center;
     gap: 20px;
}

.search-box {
    background-color:#393e46;
    /* box-shadow: 0 5px 10px rgb(36, 34, 34); */
    padding: 8px 10px;
    border-radius: 8px;
    display: flex;
    align-items: center;
}

.search-box input {
    border: none;
    outline: none;
    background: transparent;
    padding: 5px;
    color: #eeeeee;
}

.search-box .icon {
    color: #eeeeee;
    font-size: 22px;
    cursor: pointer;
    transition: 0.5s ease-in-out;
}

.notify {
    background-color:#393e46;
    padding: 10px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.notify .icon {
    font-size: 20px;
    transition: 0.5s ease-in-out;
}

.icon:hover {
    transform: scale(1.3);
    cursor: pointer;
}
