.profile {
    flex: 1;
}

.profile--header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.edit {
    background-color: #393e46;
    padding: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.edit .icon {
    font-size: 20px;
    transition: 0.5s ease-in-out;
}

.user--profile {
    height: 80%;
    margin-top: 2rem;
    background: #393e46;
    border-radius: 10px;
    text-align: center;
    display: flex;
    flex-direction: column;
    padding: 10px;
    /* justify-content: center; */
    /* align-items: center; */
}

.user--detail {
    width: 100%;
    height: 250px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.user--detail img {
    background-color: #222831;
    border-radius: 50%;
    width: 150px;
    margin-bottom: 10px;
}

.user--detail .username {
    color: #eeeeee;
}

.user-courses {
    background-color:#222831;
    flex: 1;
    border-radius: 10px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    text-align: left;
    gap: 10px;
}

.course {
    background: #393e46;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 10px;
}

.course--detail {
    display: flex;
    gap: 20px;
    align-items: center;
}

.course--cover {
    background: #222831;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    font-size: 20px;
}

.course .duration {
    font-size: 12px;
}

.course .action {
    font-weight: 900;
    font-size: 1.1rem;
}

.profession {
    color: #c5c3c3;
}