* {
  padding: 0;
  margin: 0;
}


.dashboard {
  height: 100vh;
  background-color: #393e46;
  display: flex;
  gap: 10px;
  padding: 10px;

}

.dashboard--content {
  background-color: #222831;
  flex: 1;
  border-radius: 10px;
  padding: 2rem;
  box-shadow: 0 5px 10px rgb(20, 19, 19);
  color: #eeeeee;
  display: flex;
  gap: 1.5rem;
  justify-content: space-between;
}